* {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  margin: 0;
  padding: 0;
}

.links {
  cursor: pointer;
  user-select: none;
}

.infText {
  display: flex;
  flex-wrap: wrap;
  color: white;
  transform: translateX(-100%);
  animation: slideInLeft 0.7s ease-out forwards;
}

#ContactInformationComponent {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  background-color: rgb(188, 33, 59);
  width: 100%;
}

#ContactInformationComponentLeftBlock {
  display: flex;
  flex-direction: row;
  width: 50%;
  padding: 1rem;
}


#ContactInformationComponentRightBlock {
  display: flex;
  flex-direction: row;
  width: 50%;
  width: 4rem;
}

.about_me {
  padding: 25%;
  height: 100%;
  margin-right: 2rem;
  transition: background-color 0.5s ease;
  color: white;
  animation: slideInRight 0.7s ease-out forwards;
}

.about_me:hover {
  background-color: antiquewhite;
  padding: 25%;
  height: 100%;
  margin-right: 2rem;
  color: black;
  transition: background-color 0.5s ease;
}

.notification {
  position: absolute;
  padding: 1rem;
  padding-left: 0.8rem;
  width: 15rem;
  height: 7rem;
  border-radius: 22px 22px;
  background-color: rgb(41, 145, 214);
}

.InformationAboutMe {
  position: absolute;
  top: 50px;
  right: 20px;
  width: 300px;
  max-width: 90vw;
  padding: 15px;
  background-color: antiquewhite;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;

}



@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
  }

  to {
    transform: translateX(0);
  }
}